/* アタマ　フク　クツで共通の大きさ */
/* widthはレスポンシブ対応 */
.gear-card {
  height: 205px;
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

/* ギア表示のヘッダー部分 */
/* widthはレスポンシブ対応 */
.gear-header {
  height: 2.55rem;
  background-color: rgba(0, 0, 0, 0.7);
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  color: white;
  font-size: 1.75rem;
  margin: 0px;
}

/* ギアパワーのリセットスイッチ */
.ability-reset {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 55px;
  right: 5px;
  cursor: pointer;
}
/* ホバーしたとき拡大と回転 */
.ability-reset:hover svg {
  transition: 0.1s;
  transform: rotate(15deg) scale(1.2, 1.2);
}
/* クリックで回転 */
.ability-reset:active svg {
  transition: 0.1s;
  transform: rotate(-15deg) scale(1.2, 1.2);
}
/* ツールチップ */
.ability-reset-tooltip {
  width: 7rem;
  height: 1.8rem;
  display: none;
  position: absolute;
  bottom: -1rem;
  right: 2.5rem;
  z-index: 2;
  padding: 0.3rem 0.5rem;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3rem;
  font-size: 0.9rem;
}
/* ツールチップの矢印 */
.ability-reset-tooltip:after {
  border-top: 0.3rem solid transparent;
  border-bottom: 0.3rem solid transparent;
  border-left: 0.4rem solid rgba(0, 0, 0, 0.8);
  content: '';
  display: block;
  width: 0.5rem;
  position: absolute;
  top: 0.6rem;
  right: -0.4rem;
}
/*　ホバーしたときツールチップを表示 */
.ability-reset:hover .ability-reset-tooltip {
  display: block;
}

