/*ギアパワーのスイッチ*/

.turned-on,
.turned-off {
  position: relative;
  width: 40px;
  height: 20px;
  margin-left: 20px;
}

.switch-base {
  /*position: absolute;*/
  left: 0;
  top: 0;
  /*width: 100%;*/
  /*height: 100%;*/
  cursor: pointer;
  width: 40px;
  height: 20px;
  background: #ddba2c;
  position: relative;
  display: inline-block;
  border-radius: 46px;
  transition: 0.4s;
  box-sizing: border-box;
}

.turned-off .switch-base {
  border-color: #ccc;
  background: #ccc;
}

.turned-on #switch-circle {
  position: absolute;
  width: 18px;
  height: 18px;
  background: #ffffff;
  top: 1px;
  left: 21px;
  border-radius: 9px;
  transition: 0.2s;
  cursor: pointer;
}

.turned-off #switch-circle {
  position: absolute;
  width: 18px;
  height: 18px;
  top: 1px;
  left: 1px;
  border-radius: 9px;
  transition: 0.2s;
  background: #ffffff;
  cursor: pointer;
}

.modal-close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.modal-close-btn:hover path,
.modal-close-btn-btm:hover path {
  fill: #fff;
}

