/*SCボタン*/
.color-btn,
.selected {
  margin-left: auto;
  margin-right: auto;
  height: 110px;
  width: 120px;
}

.color-btn-svg {
  cursor: pointer;
}

.color-btn-svg:hover,
.selected .color-btn-svg {
  transition-duration: 0.1s;
  transform: scale(1.05, 1.05);
}
/* 初期状態で非表示 */
.color-btn-hover {
  display: none;
}
/* 選択された状態で非表示 */
.selected .color-btn-svg .color-btn-default {
  display: none;
}
/* 選択された状態で表示 */
.selected .color-btn-svg .color-btn-hover {
  display: block;
}
/* ホバーしたとき非表示 */
.color-btn .color-btn-svg:hover .color-btn-default {
  display: none;
}
/* ホバーしたとき表示 */
.color-btn .color-btn-svg:hover .color-btn-hover {
  display: block;
}
/* マウスボタンを押し込んでいるとき表示 */
.color-btn .color-btn-svg:active .color-btn-default {
  display: block;
}
/* マウスボタンを押し込んでいるとき非表示 */
.color-btn .color-btn-svg:active .color-btn-hover {
  display: none;
}

/*
CTボタン
*/
.CT-btn,
.CT-selected {
  width: 240px;
  height: 165px;
  margin-left: auto;
  margin-right: auto;
}

.CT-btn-svg {
  width: 235px;
  height: 155px;
}

/*ボタン内のテキスト*/
.CT-text-default {
  font-size: 10rem;
  font-weight: bold;
  user-select: none;
}

.CT-text-hover {
  user-select: none;
}

.CT-btn-svg:hover .CT-text-hover {
  fill: white;
  font-size: 12rem;
  font-weight: bold;
}
/*CTボタンのアニメーション*/
.CT-btn-svg {
  cursor: pointer;
  opacity: 0.8;
}

.CT-btn-svg:hover {
  transition: 0.1s;
  transform: scale(1.05, 1.05);
}

.CT-btn-svg:hover .CT-btn-default,
.CT-btn-svg:hover .CT-text-default,
.CT-btn-svg:active .CT-btn-hover {
  fill: none;
}

.CT-btn-svg:hover .CT-btn-hover,
.CT-btn-svg:active .CT-btn-default {
  fill: #d9dc20;
}

.CT-btn-svg:active .CT-btn-hover {
  fill: none;
}

.CT-selected .CT-btn-svg {
  transform: scale(1.05, 1.05);
}

.CT-selected .CT-btn-svg .CT-btn-default {
  fill: none;
}

.CT-selected .CT-btn-svg .CT-btn-hover {
  fill: #d9dc20;
}

.CT-selected .CT-btn-svg .CT-text-default {
  fill: none;
}

.CT-selected .CT-btn-svg .CT-text-hover {
  fill: white;
  font-size: 13rem;
  font-weight: bold;
}
/*
HSボタン
*/
.HS-btn,
.HS-selected,
.HS-ML-btn,
.HS-ML-selected {
  width: 140px;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
}

.HS-btn-svg {
  width: 120px;
  height: 170px;
  cursor: pointer;
}

.OFML-HS-svg {
  width: 130px;
  height: 140px;
}

.OML-HS-svg {
  width: 110px;
  height: 150px;
}

.HS-btn-svg:hover {
  transition: 0.1s;
  transform: rotate(-15deg) scale(1.1, 1.1);
}

.HS-btn-svg:active {
  transition: 0.1s;
  transform: rotate(-15deg) scale(1, 1);
}

.HS-selected .HS-btn-svg {
  transform: rotate(-15deg) scale(1.1, 1.1);
}

.HS-selected .HS-btn-svg .HS-btn-hair {
  fill: #0cd900;
}

.HS-btn .HS-btn-svg:hover .HS-btn-hair {
  fill: #0cd900;
}

.HS-btn .HS-btn-svg:hover .HS-btn-geso-0,
.HS-btn .HS-btn-svg:hover .HS-btn-geso,
.HS-selected .HS-btn-svg:hover .HS-btn-geso-0,
.HS-selected .HS-btn-svg .HS-btn-geso-0,
.HS-selected .HS-btn-svg:hover .HS-btn-geso,
.HS-selected .HS-btn-svg .HS-btn-geso,
.HS-btn .HS-btn-svg:hover .HS-btn-suc,
.HS-selected .HS-btn-svg .HS-btn-suc {
  fill: #cff4db;
}

.HS-btn .HS-btn-svg:hover .HS-btn-geso-1,
.HS-selected .HS-btn-svg .HS-btn-geso-1 {
  fill: #63e398;
}

.HS-btn .HS-btn-svg:hover .HS-btn-hairband,
.HS-selected .HS-btn-svg .HS-btn-hairband {
  fill: #66e569;
}

.OFML-HS-btn .HS-btn-svg:hover .HS-btn-hair {
  fill: #ffb23d;
}

.HS-ML-btn .HS-btn-svg:hover .HS-btn-hair,
.HS-ML-selected .HS-btn-svg .HS-btn-hair {
  fill: #ff00b3;
}
.HS-ML-btn .HS-btn-svg:hover .HS-btn-hair-1,
.HS-ML-selected .HS-btn-svg .HS-btn-hair-1 {
  fill: #fd48ba;
}

.HS-ML-btn .HS-btn-svg:hover .HS-btn-geso,
.HS-ML-btn .HS-btn-svg:hover .HS-btn-hairband,
.HS-ML-selected .HS-btn-svg .HS-btn-geso,
.HS-ML-selected .HS-btn-svg .HS-btn-hairband {
  fill: #fecddd;
}

.HS-ML-selected .HS-btn-svg {
  transform: rotate(-15deg) scale(1.1, 1.1);
}

/*
Btmボタン
*/
.ML-Btm-btn,
.ML-Btm-selected,
.FML-Btm-btn,
.FML-Btm-selected {
  width: 130px;
  height: 150px;
  margin: auto;
}

.Btm-btn-svg {
  cursor: pointer;
}

.Btm-btn-svg:hover {
  transition: 0.1s;
  transform: rotate(-15deg) scale(1.1, 1.1);
}

.Btm-btn-svg:active {
  transition: 0.1s;
  transform: rotate(-15deg) scale(1, 1);
}

.FML-Btm-btn .Btm-btn-svg:hover .Btm-btn-color,
.FML-Btm-selected .Btm-btn-svg .Btm-btn-color {
  fill: #0cd900;
}

.ML-Btm-btn .Btm-btn-svg:hover .Btm-btn-color,
.ML-Btm-selected .Btm-btn-svg .Btm-btn-color {
  fill: #ff00b3;
}

.FML-Btm-selected .Btm-btn-svg,
.ML-Btm-selected .Btm-btn-svg {
  transform: rotate(-15deg) scale(1.1, 1.1);
}

/*  */
/*AEのスタイル*/
/*  */

#ae-display-area {
  border-radius: 30px;
}

.ae-card {
  border-bottom: dotted 3px #dee2e6;

  padding: 5px 0px;
  position: relative;
  width: 97%;
  margin-right: auto;
  margin-left: auto;
}
/* ヘッダー */
#ae-header {
  height: 2.55rem;
  background-color: rgba(0, 0, 0, 0.7);
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  color: white;
  font-size: 1.75rem;
  margin: 0px;
}

.ae-part {
  margin: auto 0px;
  padding: 0px;
}

/* ギアパワーのアイコン */
.ap-ability-icon {
  width: 40px;
  height: 40px;
  position: relative;
  position: absolute;
  top: 5px;
  left: 0px;
}

/* ギアパワーの画像 */
.ap-ability-img {
  width: 34px;
  height: 34px;
  position: absolute;
  top: 2px;
  left: 2px;
}

/* 1行のみの場合 */
.ae-text {
  text-align: right;
  margin-top: 8px;
  margin-bottom: 8px;
}

/* １つの効果を2行で書く場合の1行目 */
.ae-text-first {
  margin-top: 6px;
  margin-bottom: 0rem;
  text-align: right;
}
/* １つの効果を2行で書く場合の2行目 */
.ae-text-second {
  margin-top: 6px;
  margin-bottom: 6px;
  text-align: right;
}

.ae-colon {
  margin-top: auto;
  margin-bottom: auto;
}

.ae-effect {
  margin-top: 0rem;
  margin-bottom: 0rem;
  text-align: left;
}

.ae-sub-img {
  position: relative;
  height: 50px;
  margin-left: 20px;
  margin-right: 20px;
}

#ae-section::-webkit-scrollbar,
.gearicons-section::-webkit-scrollbar {
  width: 10px;
}

#ae-section::-webkit-scrollbar-track,
.gearicons-section::-webkit-scrollbar-track {
  background: #fff;
  border: none;
  border-radius: 10px;
  box-shadow: inset 0 0 2px #777;
}

#ae-section::-webkit-scrollbar-thumb,
.gearicons-section::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 10px;
  box-shadow: none;
}

#character-btn {
  position: absolute;
  top: 20px;
  right: 10px;
  z-index: 1;
}

/*  */
/* ブキの表示部分 */
/*  */
#weapon-card {
  width: 100%;
  height: 170px;
  background: repeating-linear-gradient(
    45deg,
    #dbe826,
    #dbe826 20px,
    #c4d412 20px,
    #c4d412 40px
  );
  border-radius: 30px;
  padding-top: 7px;
  position: relative;
}
/* ブキ表示部分のフッター */
#weapon-footer {
  width: 100%;
  height: 2rem;
  background-color: rgba(0, 0, 0, 0.8);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  position: absolute;
  bottom: 0px;
  padding-top: 0.25rem;
}
/* ツールチップ */
#weaponModal-trigger {
  cursor: pointer;
  position: relative;
}
#weaponModal-trigger:hover .weaponModal-tooltip {
  display: block;
}
.weaponModal-tooltip {
  background: rgba(0, 0, 0, 0.8);
  width: 9.5rem;
  height: 2rem;
  font-size: 1.25rem;
  border-radius: 0.3rem;
  position: absolute;
  bottom: -0.5rem;
  left: 20%;
  display: none;
}

/* サブとスペシャルの背景 */
.weapon-sub-sp {
  position: relative;
  width: 100%;
  height: 48px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 13px;
}
/* サブとスペシャルの文字 */
.weapon-sub-sp p {
  margin-top: 10px;
  margin-bottom: 10px;
}

/*  */
/* ツールチップ */
/*  */

/* ギア表示部分のツールチップ */
.gear-tooltip {
  width: 7rem;
  height: 1.8rem;
  display: none;
  position: absolute;
  bottom: 4.25rem;
  left: 0rem;
  z-index: 2;
  padding: 0.3em 0.5em;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3em;
  font-size: 0.9rem;
}

.gear-tooltip:after {
  width: 0.5rem;
  content: '';
  display: block;
  position: absolute;
  left: 3.25rem;
  top: 1.75rem;
  border-left: 0.3rem solid transparent;
  border-right: 0.3rem solid transparent;
  border-top: 0.5rem solid rgba(0, 0, 0, 0.8);
}

.gear-img:hover .gear-tooltip {
  display: block;
}

/* メインスロットとサブスロットのツールチップ */
.slot-tooltip {
  width: 8.8rem;
  height: 1.8rem;
  /* display: none; */
  position: absolute;
  top: -2.2rem;
  left: -4.35rem;
  z-index: 2;
  padding: 0.3em 0.5em;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3em;
  font-size: 0.9rem;
}
/* 右のサブスロットのツールチップ */
.slot-tooltip-right {
  width: 8.8rem;
  height: 1.8rem;
  position: absolute;
  top: -2.2rem;
  left: -6.5rem;
  z-index: 2;
  padding: 0.3em 0.5em;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3em;
  font-size: 0.9rem;
}
/* メインスロットのツールチップ */
.slot-tooltip-left {
  width: 8.8rem;
  height: 1.8rem;
  position: absolute;
  top: -2.2rem;
  left: -3rem;
  z-index: 2;
  padding: 0.3em 0.5em;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3em;
  font-size: 0.9rem;
}

/* 上側に表示する場合だとフォントサイズ変更の際に左右の表示位置が*/
/* 大きくズレやすくなるので矢印を別に作る */
.tooltip-top-arrow {
  width: 0.5rem;
  display: none;
  z-index: 2;
  border-top: 0.4rem solid rgba(0, 0, 0, 0.8);
  border-right: 0.3rem solid transparent;
  border-left: 0.3rem solid transparent;
  position: absolute;
  top: -0.85rem;
  left: 37%;
}

.ability-icon:hover .slot-tooltip,
.ability-icon:hover .tooltip-top-arrow {
  display: block;
}

/* ギアモーダル内のツールチップ */
.gear-icon-tooltip {
  height: 1.8rem;
  display: none;
  z-index: 6;
  padding: 0.2rem 0.4rem;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 0.3em;
  font-size: 0.9rem;
}

.gear-icon:hover .gear-icon-tooltip-arrow,
.gear-icon:hover .gear-icon-tooltip {
  display: inline;
}

.tooltip-container {
  position: absolute;
  top: -2rem;
  left: -100px;
  width: 200px;
}

.gear-icon-tooltip-arrow {
  width: 0.5rem;
  display: none;
  z-index: 2;
  border-top: 0.4rem solid rgba(0, 0, 0, 0.8);
  border-right: 0.3rem solid transparent;
  border-left: 0.3rem solid transparent;
  position: absolute;
  top: -0.55rem;
  left: 47%;
}

/*  */
/* アビリティモーダル関連 */
/*  */

/* モーダル上部の矢印 */
.mainSlotModal:before {
  border-right: 10px solid transparent;
  border-bottom: 20px solid #6b6b6b;
  border-left: 10px solid transparent;
  z-index: 6;
  display: block;
  content: '';
}

/* サブスロットモーダル */
.subSlotModal-left {
  z-index: 5;
}
.subSlotModal-mid {
  z-index: 5;
}
.subSlotModal-right {
  z-index: 5;
}
.subSlotModal-left:before {
  border-right: 10px solid transparent;
  border-bottom: 20px solid #6b6b6b;
  border-left: 10px solid transparent;
  z-index: 6;
  display: block;
  content: '';
}
.subSlotModal-mid:before {
  border-right: 10px solid transparent;
  border-bottom: 20px solid #6b6b6b;
  border-left: 10px solid transparent;
  z-index: 6;
  display: block;
  content: '';
}
.subSlotModal-right:before {
  border-right: 10px solid transparent;
  border-bottom: 20px solid #6b6b6b;
  border-left: 10px solid transparent;
  z-index: 6;
  display: block;
  content: '';
}

/* アビリティモーダルにおいて共通のスタイル */
.ability-modal {
  border-radius: 10px;
  margin-left: 0px;
  margin-right: 0px;
  background: repeating-linear-gradient(
    45deg,
    #808080,
    #808080 20px,
    #6b6b6b 20px,
    #6b6b6b 40px
  );
  display: none;
  padding-left: 10px;
}
/* アビリティアイコンの大きさ */

.ability {
  width: 50px;
  height: 50px;
  position: relative;
  margin: 5px;
  cursor: pointer;
}

.abilitymodal-img {
  width: 46px;
  height: 46px;
  position: absolute;
  top: 1px;
  left: 1px;
}
